<template>
  <div class="bg">
    <el-carousel indicator-position="none" height="100vh">
      <el-carousel-item v-for="item in hbimg" :key="item.id">
        <img class="hbimg" :src="item.img"/>
      </el-carousel-item>
    </el-carousel>
    <div class="login">
      <span class="guanbi" @click="guanbi()"></span>
      <div class="box">
        <h3>找回密码-验证手机号</h3>
        <el-form :model="ruleForm" status-icon ref="ruleForm" label-width="0px" class="demo-ruleForm">
          <el-form-item prop="name">
            <el-input class="dlinput" placeholder="输入11位手机号码" type="tel" v-model="ruleForm.phone" autocomplete="off"
                      clearable></el-input>
          </el-form-item>
          <el-form-item class="dlinput" prop="verify">
            <el-input style="width: 164px;" type="text" placeholder="输入6位验证码" v-model="ruleForm.verify"
                      autocomplete="off"
                      clearable></el-input>
            <el-button @click="getCode()">{{btnText}}</el-button>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <router-link style="float: left;" to="/login">返回登录</router-link>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <el-button class="dlbtn" @click="login()">下一步</el-button>
          </el-form-item>
        </el-form>

      </div>
    </div>
  </div>
</template>

<script>
  import {sendCode} from '@/api/login'
  import {Message} from "element-ui";

  export default {
    data() {
      const validatePass = (rule, value, callback) => {
        if (value.length < 6) {
          callback(new Error('验证码不能小于6位'))
        } else {
          callback()
        }
      };
      return {
        hbimg: [
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg1.jpg"),
            id: 0
          },
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg2.jpg"),
            id: 1
          },
          {
            img: ("https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/bg3.jpg"),
            id: 2
          }
        ],
        btnText: '获取验证码',
        countdown: 60,
        dialogVisible: false,
        ruleForm: {
          phone: '',
          verify: ''
        },
        loginRules: {
          phone: [{requird: true, trigger: 'blur'}],
          verify: [{requird: true, trigger: 'blur', validator: validatePass}]
        },
      };
    },
    methods: {

      login() {
        // loginByCode(this.ruleForm.phone,this.ruleForm.verify).then(response => {
        //   let res = response.data;
        //   if (res.success) {
        //     this.getUser();
        //   } else {
        //     Message({
        //       message: response.message,
        //       type: 'error',
        //       duration: 5 * 1000
        //     })
        //   }
        // }).catch(error => {
        // })
        this.$refs.ruleForm.validate(valid => {
          if (valid) {
            this.$store.dispatch('LoginByCode', this.ruleForm).then(() => {
              this.getUser()
            }).catch(() => {

            })
          } else {
            return false
          }
        })
      },
      getUser() {
        this.$store.dispatch('GetUserInfo').then(response => {
          this.$router.push({path: this.redirect || '/resetpwd'})
        }).catch(error => {
        })
      },
      getCode() {
        if (this.ruleForm.phone == '') {
          this.$message.error('请输入手机号');
          return;
        }
        this.sendsms();
        this.setTime();//开始倒计时
      },
      //60s倒计时实现逻辑
      setTime() {
        if (this.countdown == 0) {
          this.btnText = "获取验证码";
          this.countdown = 60;//60秒过后button上的文字初始化,计时器初始化;
          return;
        } else {
          setTimeout(this.setTime, 1000);//每1000毫秒执行一次
          this.btnText = "(" + this.countdown + "s)后重新发送";
          this.countdown--;
        }
      },
      sendsms() {
        sendCode(this.ruleForm.phone, 3).then(response => {
          let res = response.data;
          if (res.success) {
            this.setTime();//开始倒计时
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      guanbi() {
        this.$router.push({path: this.redirect || '/'})
      },
    }
  }
</script>


<style scoped>
  .bg {
    height: 100%;
    min-height: 100vh;
  }

  .login {
    position: absolute;
    top: 220px;
    right: 280px;
    width: 400px;
    height: 350px;
    background-color: #fff;
    z-index: 9999;
    border-radius: 6px;
  }

  .box {
    margin: 54px 60px 0;
    text-align: center;
  }

  .box > p {
    display: flex;
    justify-content: space-between;
    padding: 0 12px;
    font-size: 13px;
  }

  .box > p > a:last-child {
    color: #999;
  }

  .dlbtn {
    width: 280px;
    height: 45px;
    background-color: #ffd800;
    border-radius: 5px;
    color: #fff;
    font-size: 17px;
    margin: 0px 0 10px;
    border: 0;
  }

  .login h3 {
    text-align: center;
    font-size: 19px;
    font-weight: 700;
    color: #333;
    letter-spacing: 1px;
    margin-bottom: 20px;
  }

  .guanbi {
    position: absolute;
    top: 25px;
    right: 25px;
    width: 12px;
    height: 12px;
    background: url("../../static/guanbi.png");
  }

  .tongyi span {
    color: #999;
  }

  .registe {
    position: absolute;
    bottom: 0;
    width: 400px;
    height: 50px;
    background-color: #ddd;
    text-align: center;
    line-height: 50px;
    font-size: 14px;
    letter-spacing: 1px;
  }

  .registe > p {
    font-weight: 700;
  }

  .registe > p > span {
    color: #999;
    font-weight: 400;
  }

  .dlinput .el-button--medium {
    padding: 10px 0;
    width: 112px;
  }
</style>



